import React from 'react'
import { Box, Stack, Typography } from '@mui/material'
import logo from '../../assets/logo.png'

const Footer = () => {
  return (
    <Box mt="20px" bgcolor="">
    <Stack gap="20px" alignItems='center' px="20px" pt="12px">
<img src={logo} width="100px" height="40px" alt='logo'/>
<Typography variant='h5' pb='20px' mt='10px' fontWeight="bold">
  Made with ❤️ by Rushi Chandvale
</Typography>
    </Stack>
    </Box>
  )
}

export default Footer